<template>
  <f7-page class="me-sign-up" no-toolbar>
    <f7-navbar back-link="" sliding title=""></f7-navbar>

    <f7-list no-hairlines-md>
      <f7-list-input
        :value="nickname"
        label=""
        type="text"
        placeholder=""
        error-message=""
        :error-message-force="showNicknameErrorFlag"
      ></f7-list-input>

      <f7-list-input
        :value="gender"
        label=""
        type="select"
        error-message=""
        :error-message-force="showGenderErrorFlag"
      >
        <option value="1"></option>
        <option value="2"></option>
      </f7-list-input>

      <f7-list-input label="" :input="false">
        <f7-range :value="age" label slot="input" :min="1" :max="100" :step="1"></f7-range>
      </f7-list-input>

      <f7-list-input :value="vaccineStatus" label="" type="select">
        <option value="1"></option>
        <option value="2"></option>
      </f7-list-input>

      <f7-list-input label="" :value="petType" type="text" placeholder=""></f7-list-input>

      <f7-list-input label="" :value="type" type="text" placeholder=""></f7-list-input>
    </f7-list>

    <div class="block block-strong">
      <div class="block-header"></div>
      <picture-input
        ref="pictureInput"
        @change="onChange"
        width="200"
        height="200"
        margin="16"
        accept="image/*"
        size="10"
        hideChangeButton
        :removable="true"
        :customStrings="{upload: '<p></p>', tap: '<p></p>', remove: ''}"
      ></picture-input>
    </div>

    <f7-block>
      <f7-row>
        <f7-button class="col" raised @click="back"></f7-button>
        <f7-button class="col" fill raised color="green" @click="submitPetData"></f7-button>
      </f7-row>
    </f7-block>
  </f7-page>
</template>

<script>
import { addPet } from '../utils/index.js'
import PictureInput from 'vue-picture-input'

export default {
  components: {
    PictureInput
  },
  data() {
    return {
      nickname: '',
      showNicknameErrorFlag: false,
      gender: 0,
      showGenderErrorFlag: false,
      age: 0,
      vaccineStatus: 0,
      petType: 0,
      type: '',
      picture: ''
    }
  },
  methods: {
    onChange(image) {
      this.picture = image
    },
    toast(msg) {
      const toast = this.$f7.toast.create({
        text: msg,
        closeTimeout: 2000,
        position: 'center'
      })
      toast.open()
    },
    async submitPetData() {
      try {
        setTimeout(() => {
          this.showNicknameErrorFlag = false
          this.showGenderErrorFlag = false
        }, 3000)

        if (!this.nickname) {
          return (this.showNicknameErrorFlag = true)
        }

        if (!this.gender) {
          return (this.showGenderErrorFlag = true)
        }

        if (!this.picture) {
          return this.toast('')
        }

        this.$f7.preloader.show()

        const result = await addPet(this)

        this.$f7.preloader.hide()

        if (!result) {
          console.log('', result)
          this.toast('')
          return
        }

        this.toast('')
        this.resetAllProps()

        // setTimeout(() => {
        //   this.back()
        // }, 1000)
      } catch (error) {
        console.error(error)
        this.$f7.preloader.hide()
        this.toast('')
      }
    },
    back() {
      this.$f7router.back()
    },
    resetAllProps() {
      this.nickname = ''
      this.gender = 0
      this.age = 0
      this.vaccineStatus = 0
      this.petType = 0
      this.type = ''
      this.picture = ''
    }
  }
}
</script>

