<template>
  <f7-page name="personDetail" no-toolbar @page:afterin="onPageAfterIn">
    <f7-navbar :back-link="backText" sliding title=""></f7-navbar>

    <f7-block-title></f7-block-title>
    <f7-list class="me-person-detail-list" v-if="userInfo">
      <f7-list-item header="" :title="userInfo.name"></f7-list-item>
      <f7-list-item header="" :title="userInfo.nickname"></f7-list-item>
      <f7-list-item header="" :title="userInfo.gender"></f7-list-item>
      <f7-list-item header="" :title="userInfo.age"></f7-list-item>
      <f7-list-item media-item header="" :text="userInfo.homeAddress"></f7-list-item>
      <f7-list-item media-item header="" :text="userInfo.workAddress"></f7-list-item>
    </f7-list>

    <f7-block v-else inset></f7-block>
  </f7-page>
</template>

<script>
import { getUserInfo } from '../utils'

export default {
  data() {
    return {
      backText: '',
      userInfo: null
    }
  },
  methods: {
    async onPageAfterIn() {
      try {
        const data = await getUserInfo()

        if (data.iRet === 0) {
          this.userInfo = data.userInfo
        } else {
          console.error(data)
        }
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

<style scoped>
.me-person-detail-list {
  word-break: break-all;
}
</style>
